<template>
  <li 
  class="menu__item" 
  :class="{'selected': routeIdx === $store.state.selectedIdx}"
  v-for="(route, routeIdx) in routes"
  >
    <router-link 
    :to="route.path"
    @mouseover="itemHover(routeIdx)"
    >..</router-link>
  </li>
</template>

<script>

export default {
  data() {
    return {
      routes: this.$router.options.routes.filter(item => item.name === 'home')
    }
  },
  methods: {
    itemHover(idx) {
      this.$store.commit('setInfo', {name: this.routes[idx].name, date: this.routes[idx].meta.date})
      this.$store.commit('setSelectedIdx', idx)
    }
  }
}
</script>
